import React from "react";

const Menu = ({ items }) => {
  return (
    <div className="section-center">
      {items.map((menuItem) => {
        const { id, title, githubRepo, link, img } = menuItem;
        return (
          <article key={id} className="menu-item">
            <img
              src={process.env.PUBLIC_URL + img}
              alt={title}
              className="photo"
            />
            <div className="item-info">
              <header>
                <h4>{title}</h4>
              </header>
              <br />
              <a className="filter-btn" href={link} target="_blank">
                Visit project
              </a>
              <br />
              <br />
              <a className="filter-btn" href={githubRepo} target="_blank">
                Github Repo
              </a>
            </div>
          </article>
        );
      })}
    </div>
  );
};

export default Menu;
